"use client"

import { CheckCircle, AlertTriangle, XCircle, FileText, ChevronDown, ChevronUp } from "lucide-react"
import { Button } from "@/components/ui/button"
import { useState } from "react"
import { cn } from "@/lib/utils"

interface AnalysisItem {
  status: "good" | "warning" | "missing"
  text: string
}

interface AnalysisResultsProps {
  fileName: string
  wordCount: number
  stepCount: number
  sectionCount: number
  analysisTime: number
  strengths: AnalysisItem[]
  improvements: AnalysisItem[]
  readabilityScore: number
  completeness: { current: number; total: number }
  originalContent: string
  onStartImprovement: () => void
}

export function AnalysisResults({
  fileName,
  wordCount,
  stepCount,
  sectionCount,
  analysisTime,
  strengths,
  improvements,
  readabilityScore,
  completeness,
  originalContent,
  onStartImprovement,
}: AnalysisResultsProps) {
  const [showFullContent, setShowFullContent] = useState(false)

  const getStatusIcon = (status: AnalysisItem["status"]) => {
    switch (status) {
      case "good":
        return <CheckCircle className="w-4 h-4 text-green-600" />
      case "warning":
        return <AlertTriangle className="w-4 h-4 text-yellow-600" />
      case "missing":
        return <XCircle className="w-4 h-4 text-red-600" />
    }
  }

  return (
    <div className="space-y-6">
      {/* Status Indicators */}
      <div className="flex flex-wrap gap-3">
        <div className="flex items-center gap-2 text-sm text-green-700 bg-green-50 px-3 py-1.5 rounded-full">
          <CheckCircle className="w-4 h-4" />
          File Uploaded: {fileName}
        </div>
        <div className="flex items-center gap-2 text-sm text-green-700 bg-green-50 px-3 py-1.5 rounded-full">
          <CheckCircle className="w-4 h-4" />
          Extracted: {wordCount} words, {stepCount} steps, {sectionCount} sections
        </div>
        <div className="flex items-center gap-2 text-sm text-green-700 bg-green-50 px-3 py-1.5 rounded-full">
          <CheckCircle className="w-4 h-4" />
          Analysis complete in {analysisTime}s
        </div>
      </div>

      {/* Analysis Card */}
      <div className="bg-white border border-gray-200 rounded-xl p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
          <FileText className="w-5 h-5 text-blue-600" />
          Current SOP Structure Analysis
        </h3>

        <div className="grid md:grid-cols-2 gap-6">
          {/* Strengths */}
          <div>
            <h4 className="font-medium text-gray-700 mb-3">Strengths Found:</h4>
            <ul className="space-y-2">
              {strengths.map((item, index) => (
                <li key={index} className="flex items-start gap-2 text-sm">
                  {getStatusIcon(item.status)}
                  <span className="text-gray-600">{item.text}</span>
                </li>
              ))}
            </ul>
          </div>

          {/* Improvements */}
          <div>
            <h4 className="font-medium text-gray-700 mb-3">Areas for Improvement:</h4>
            <ul className="space-y-2">
              {improvements.map((item, index) => (
                <li key={index} className="flex items-start gap-2 text-sm">
                  {getStatusIcon(item.status)}
                  <span className="text-gray-600">{item.text}</span>
                </li>
              ))}
            </ul>
          </div>
        </div>

        {/* Scores */}
        <div className="flex flex-wrap gap-6 mt-6 pt-6 border-t border-gray-100">
          <div>
            <span className="text-sm text-gray-500">Readability Score:</span>
            <span
              className={cn(
                "ml-2 font-semibold",
                readabilityScore >= 70 ? "text-green-600" : readabilityScore >= 50 ? "text-yellow-600" : "text-red-600",
              )}
            >
              {readabilityScore}/100 {readabilityScore >= 70 ? "(Good)" : readabilityScore >= 50 ? "(Fair)" : "(Poor)"}
            </span>
          </div>
          <div>
            <span className="text-sm text-gray-500">Completeness:</span>
            <span
              className={cn(
                "ml-2 font-semibold",
                completeness.current / completeness.total >= 0.7
                  ? "text-green-600"
                  : completeness.current / completeness.total >= 0.5
                    ? "text-yellow-600"
                    : "text-red-600",
              )}
            >
              {completeness.current}/{completeness.total} essential sections
            </span>
          </div>
        </div>
      </div>

      {/* Original Content Preview */}
      <div className="bg-white border border-gray-200 rounded-xl p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
          <FileText className="w-5 h-5 text-gray-600" />
          Original SOP Preview
        </h3>
        <div className="bg-gray-50 rounded-lg p-4 font-mono text-sm text-gray-700 max-h-48 overflow-y-auto">
          <pre className="whitespace-pre-wrap">
            {showFullContent ? originalContent : originalContent.slice(0, 500) + "..."}
          </pre>
        </div>
        <Button
          variant="ghost"
          onClick={() => setShowFullContent(!showFullContent)}
          className="mt-3 text-blue-600 hover:text-blue-700"
        >
          {showFullContent ? (
            <>
              Collapse <ChevronUp className="w-4 h-4 ml-1" />
            </>
          ) : (
            <>
              Expand to View Full Document <ChevronDown className="w-4 h-4 ml-1" />
            </>
          )}
        </Button>
      </div>

      {/* CTA */}
      <div className="flex justify-end">
        <Button onClick={onStartImprovement} className="bg-blue-600 hover:bg-blue-700 text-white px-6">
          Start Improvement Chat
        </Button>
      </div>
    </div>
  )
}
