"use client"

import { useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { ArrowLeft, HelpCircle } from "lucide-react"
import { Button } from "@/components/ui/button"
import { DragDropZone } from "@/components/upload/drag-drop-zone"
import { AnalysisResults } from "@/components/upload/analysis-results"

type Phase = "upload" | "analyzing" | "results"

const mockAnalysis = {
  fileName: "invoice_approval.pdf",
  wordCount: 847,
  stepCount: 23,
  sectionCount: 4,
  analysisTime: 2.3,
  strengths: [
    { status: "good" as const, text: "Has clear purpose statement" },
    { status: "good" as const, text: "Contains step-by-step procedures" },
    { status: "good" as const, text: "Defined scope section" },
  ],
  improvements: [
    { status: "warning" as const, text: "Missing troubleshooting section" },
    { status: "warning" as const, text: "Roles and responsibilities not clearly defined in each step" },
    { status: "warning" as const, text: "No quality check or success criteria" },
    { status: "missing" as const, text: "No visual aids or flowcharts" },
    { status: "missing" as const, text: "Missing glossary/definitions section" },
  ],
  readabilityScore: 72,
  completeness: { current: 6, total: 12 },
  originalContent: `# Invoice Approval Procedure

## Purpose
This document outlines the standard process for approving invoices in the finance department.

## Scope
Applies to all invoices over $500 that require manager approval.

## Procedure
1. Receive invoice via email or mail
2. Log invoice in the tracking spreadsheet
3. Verify invoice details against purchase order
4. Route to appropriate manager based on amount
5. Manager reviews and approves or rejects
6. If approved, schedule for payment
7. Update tracking spreadsheet with status
8. Archive invoice documentation

## Notes
- All invoices must be processed within 5 business days
- Disputed invoices should be escalated to the Finance Director`,
}

export default function ImproveSOPPage() {
  const router = useRouter()
  const [phase, setPhase] = useState<Phase>("upload")
  const [pastedText, setPastedText] = useState("")

  const handleFileSelect = (file: File) => {
    // Simulate analysis
    setPhase("analyzing")
    setTimeout(() => {
      setPhase("results")
    }, 2000)
  }

  const handleAnalyze = () => {
    if (pastedText.trim()) {
      setPhase("analyzing")
      setTimeout(() => {
        setPhase("results")
      }, 2000)
    }
  }

  const handleStartImprovement = () => {
    router.push("/create?mode=improve")
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200 px-4 py-3 flex items-center justify-between">
        <Link href="/dashboard" className="text-gray-600 hover:text-gray-900 flex items-center gap-2">
          <ArrowLeft className="w-5 h-5" />
          <span className="hidden sm:inline">Back</span>
        </Link>
        <h1 className="text-lg font-semibold text-gray-900">Improve Existing SOP</h1>
        <Button variant="ghost" size="sm" className="gap-2">
          <HelpCircle className="w-4 h-4" />
          <span className="hidden sm:inline">Help</span>
        </Button>
      </header>

      <main className="max-w-4xl mx-auto px-4 py-8">
        {phase === "upload" && (
          <div className="space-y-8">
            <div className="text-center">
              <h2 className="text-2xl font-bold text-gray-900 mb-2">Upload Your Current SOP</h2>
              <p className="text-gray-600">Upload a file or paste your SOP text for AI analysis and improvement</p>
            </div>

            <DragDropZone onFileSelect={handleFileSelect} />

            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-200" />
              </div>
              <div className="relative flex justify-center">
                <span className="px-4 bg-gray-50 text-gray-500 text-sm">OR</span>
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Paste your SOP text directly:</label>
              <textarea
                value={pastedText}
                onChange={(e) => setPastedText(e.target.value)}
                placeholder="Paste your SOP text here... Include the title, steps, and any other content."
                className="w-full h-48 p-4 border border-gray-300 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none font-mono"
              />
              <div className="flex justify-end mt-4">
                <Button
                  onClick={handleAnalyze}
                  disabled={!pastedText.trim()}
                  className="bg-blue-600 hover:bg-blue-700 text-white disabled:opacity-50"
                >
                  Analyze SOP
                </Button>
              </div>
            </div>
          </div>
        )}

        {phase === "analyzing" && (
          <div className="text-center py-16">
            <div className="w-16 h-16 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-6" />
            <h2 className="text-xl font-semibold text-gray-900 mb-2">Analyzing Your SOP...</h2>
            <p className="text-gray-600">Extracting structure and identifying improvements</p>
          </div>
        )}

        {phase === "results" && (
          <div>
            <div className="text-center mb-8">
              <h2 className="text-2xl font-bold text-gray-900 mb-2">Analysis Complete</h2>
              <p className="text-gray-600">Review the findings below and start improving your SOP</p>
            </div>
            <AnalysisResults {...mockAnalysis} onStartImprovement={handleStartImprovement} />
          </div>
        )}
      </main>
    </div>
  )
}
